<template>
  <aside
    class="db-aside px-2"
    :class="{'hide':isHide}"
  >
    <ul>
      <li class="pt-3">
        <div>
          <p class="db-aside__title pt-1 pb-3 pl-3">НАВИГАЦИЯ</p>
          <a
            class="db-aside__item"
            @click="goHome(null)"
          >
            <i class="menu-item-icon icon ion-ios-home-outline mr-2"></i>
            <p> Fine trading </p>
          </a>
        </div>
      </li>
      
      <li v-if="!curProjDashboard" class="pt-3">
        <div>
          <p class="db-aside__title pt-3 pb-2 pl-3">ПОЛЬЗОВАТЕЛЬ</p>
          <a class="db-aside__item" href="#">
            <i class="menu-item-icon icon ion-ios-book-outline mr-2"></i>
            <p> Мои заказы </p>
          </a>
        </div>
      </li>

      <li class="pt-3">
        <div>
          <p class="db-aside__title pt-3 pb-2 pl-3">
            УПРАВЛЕНИЕ БИЗНЕСОМ
          </p>
          <router-link
            v-for="link in businesses"
            :key="link.link"
            class="db-aside__item"
            :to='`${link.link}/dashboard/`'
          >
            <i class="menu-item-icon icon ion-ios-briefcase-outline mr-2"></i>
            <p>{{ link.title }}</p>
          </router-link>
          <a
            @click="TOGGLE_DB_MODAL"
            class="db-aside__item pointer"
          >
            <i class="menu-item-icon icon ion-ios-plus-outline mr-2"></i>
            <p> Добавить бизнес </p>
          </a>
        </div>
      </li>

      <li v-if="curProjDashboard" class="pt-3">
        <div>
          <p class="db-aside__title pt-3 pb-2 pl-3">ЗАПИЧКО</p>
          <a
            v-for="tab in tabs"
            :key="tab.link"
            @click="CHANGE_TAB_PROJ(tab.link)"
            :class="{'active': curProjTab === tab.link}"
            class="db-aside__item pointer"
          >
            <i :class="`menu-item-icon icon ${tab.icon} mr-2`"></i>
            <p> {{ tab.title }} </p>
          </a>
        </div>
      </li>

    </ul>
  </aside>
</template>

<script>

import { mapMutations, mapGetters, mapState } from 'vuex';

const businesses = () => ([
  {
    title: 'Запичко',
    link: '/Запичко'
  },
])

const tabs = () => ([
  {
    title: 'Информация',
    link: 'DashInfo',
    icon: 'ion-ios-speedometer-outline'
  },
  {
    title: 'Заказы',
    link: 'DashOrders',
    icon: 'ion-clipboard'
  },
    {
    title: 'Товары',
    link: 'DashProduct',
    icon: 'ion-ios-cart-outline'
  },
  {
    title: 'Новини',
    link: 'DashNews',
    icon: 'ion-ios-paper-outline'
  },
  {
    title: 'Налаштування',
    link: 'DashSettings',
    icon: 'ion-ios-gear-outline'
  }
])

export default {
  name: 'db-sidebar',
  data() {
    return {
      businesses: businesses(),
      tabs: tabs()
    }
  },
  computed: {
    ...mapState([
      'curProjDashboard',
      'curProjTab'
    ]),
    ...mapGetters([
      'USER_INFO'
    ])
  },
  props: {
    isHide: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    ...mapMutations([
      'TOGGLE_DB_SIDEBAR',
      'SET_INFO_PROJ',
      'CHANGE_TAB_PROJ',
      'TOGGLE_DB_MODAL'
    ]),
    goHome(val) {
      this.$router.push('/dashboard')
      this.SET_INFO_PROJ(val)
      this.CHANGE_TAB_PROJ('DashInfo')
    }
  }
}
</script>

<style lang="scss">
  
  .icon {
    font-size:22px;
    font-weight:200;
    width:18px;
  }

  .db-aside {
    position: fixed;
    left: 0;
    background: #4296d0;
    color: #fff;
    width: 230px;
    height: 100%;
    transition: 0.3s ease width;
    z-index: 1350;

    p {
      letter-spacing: 0.2px;
      font-size: .85rem;
      font-weight: 300;
    }

    @media (max-width: 768px) {
      div.db-layout__inner & {
        width: 230px;
        padding: 0 .5rem !important;
        &.hide {
          width: 0;
          padding: 0 !important;

          ul {
            display: none;
          }
        }
        ul {
          display: block; 
        }
      }
    }

    &.hide {
      width: 60px;

      .db-aside__title {
        opacity: 0;
        visibility: hidden;
      }

      .db-aside__item {
        padding: 0;
        justify-content: center;
        i {margin: 0 !important;}
        p {display: none;}

        &.active {
          background: #00000022 !important;
          color: #fff !important;
        }
      }
      &:hover {
        width: 230px;

        .db-aside__title {
          opacity: .3;
          visibility: visible;
        }

        .db-aside__item {
          padding-left: 1rem;
          justify-content: start;
          i {margin-right: .5rem !important;}
          p {display: inline;}
        }
      }
    }

    div &__title {
      font-family: "Roboto", "Helvetica Neue", Arial, sans-serif;
      font-size: 10px;
      opacity: .3;
      color: #fff;
      white-space: nowrap;
    }

    &__item {
      display: flex;
      align-items: center;
      height: 40px;
      padding-left: 1rem;
      white-space: nowrap;
      font-size: 0.875rem;
      line-height: 1.5;
      color: #fff;

      &:hover {
        background: #00000022;
        color: #fff;
      }
                
      &.active {
        background: #00000022 !important;
        color: #fff !important;
      }
    }
  }
</style>